<template lang="html">
  <div class="table-frame">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>{{content.title}}</span>
        <span class="relation-ship">关系:{{content.relationShip}}</span>
      </div>
      <!-- <div v-for="o in 4" :key="o" class="text item">
        {{'列表内容 ' + o }}
      </div> -->
      <slot></slot>
    </el-card>
  </div>
</template>

<script>
export default {
  props:{
    content:{
      type:Object,
      default:()=>{}
    }
  },
  created() {
    console.log(this.content)
  }
}
</script>

<style lang="stylus" scoped>
.box-card
  font-size 14px
  margin-bottom 20px
  .relation-ship
    margin-left 10px
</style>



