
<template lang="html">
  <div class="rule-config">
    <h2 class="sort-title">
      <span>排序:<i>1</i></span>
      <span>名称：<i>增加积分</i></span>
    </h2>
    <div class="table-wrapper clearfix">
       <div class="table-content">
        <el-table :data="tableData" border style="width: 100%">
          <el-table-column prop="date" label="参数">
          </el-table-column>
          <el-table-column prop="name" label="设定值">
          </el-table-column>
        </el-table>
      </div>
      <div class="jump">
        <span class="el-icon-download"></span>
      </div>
    </div>



  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [
        {
          date: "注册",
          name: "20"
        },
        {
          date: "年龄",
          name: "30"
        }
      ]
    };
  }
};
</script>
<style lang='stylus'>
.clearfix
  display block
  content ""
  clear both
  overflow hidden


.rule-config
  .sort-title
    font-size 14px;
    padding 15px 0;
    border-bottom 1px dotted #ddd
    margin-bottom 10px
    i
      font-style normal
  .table-wrapper
    position relative
    .table-content
      float left
    .jump
      position absolute
      right 0
      top 50%
      margin-top -18px
      // height 145px
      // line-height 145px
      span
        display inline-block
        font-size 36px
        color #409eff
        cursor pointer






</style>



