<template lang="html">
  <div class="toptab-wrapper">
    <div class="toptab-item"
         :class="{active : nowIndex == index}"
         v-for="(item,index) in topTabData"
         :key="item.value"
         @click="handleRouter(item.router,index)">
      <router-link :to="{ name: item.router, params: {} }" class="router-link-toptab">
        <i class="iconfont" :class="item.iconfont"></i>
        <span>{{item.text}}</span>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      nowIndex:0
    }
  },
  props:{
    topTabData:{
      type:Array,
      required:true
    }
  },
  methods:{
    handleRouter(router,index) {
      this.$emit("on-link",router)
      this.nowIndex = index
    }
  }
}
</script>

<style lang="stylus" scoped>
.toptab-wrapper
  display flex
  width 30%
  height 70px
  align-content center
  align-items center
  margin 0 auto

  .toptab-item
    &.active
      .router-link-toptab
        color #409eff !important
    text-align center
    // flex-wrapper
    flex 1
    font-size 14px

    .iconfont
      font-size 26px
    .router-link-toptab
      display flex
      flex-direction column
      text-decoration none
      color #303133
    :hover
      color #409eff
</style>



