<template lang="html">
  <div class="rule-config">
    <!-- <div class="top-wrapper">
      <top-tab :top-tab-data="toptabData"
              @on-link="handleRouter">
      </top-tab>
    </div> -->
    <el-row :gutter="20" style="height:100%;">
      <el-col :span="6">
        <div class="rules">
          <header-title></header-title>
          <c-rule-config></c-rule-config>
        </div>
      </el-col>
      <el-col :span="12">
        <div class="content">
          <div class="all-info">
            <information></information>
          </div>
          <router-view></router-view>
        </div>
      </el-col>
      <el-col :span="6" class="right-col-wrapper">
        <div class="right-wrapper">
          <!-- 3种类型 -->
          <table-frame  v-for="tableItem in ruleConfigRightTableData"
                        :key="tableItem.id"
                        :content="tableItem">
                        <!-- 每一种类型下有N条数据 N个表格 -->
                        <!-- listItem Array类型 -->
                        <el-table v-for="listItem in tableItem.message.tableData"
                                  :key="listItem.id"
                                  :data="listItem"
                                  class="table-frame-table-item"
                                  border>
                                  <el-table-column v-for="column in rightListColumn"
                                                   :key="column.prop"
                                                   :prop="column.prop"
                                                   :label="column.label">
                                  </el-table-column>
                        </el-table>
          </table-frame>
        </div>
      </el-col>

    </el-row>

  </div>
</template>

<script>
import TopTab from "@/components/base/topTab"
import CRuleConfig from "@/components/ruleConfig"
import HeaderTitle from "@/components/HeaderTitle"
import TableFrame from "@/components/ruleConfig/TableFrame"
import Information from "@/components/Information"

import {
  mapState
} from "vuex"

export default {
  components: {
    TopTab,
    CRuleConfig,
    HeaderTitle,
    TableFrame,
    Information
  },
  data() {
    return {
      toptabData: [{
          iconfont: "icon-user",
          text: "会员",

          router: "memberRuleConfig"
        },
        {
          iconfont: "icon-category",
          text: "订单",
          router: "orderRuleConfig"
        },
        {
          iconfont: "icon-biaoqianguanli1",
          text: "标签",
          router: "tagRuleConfig"
        }
      ],
      rightListColumn: [{
        prop: "attribute",
        label: "属性"
      }, {
        prop: "operate",
        label: "操作符"
      }, {
        prop: "value",
        label: "设定值"
      }],
      tableData: [{

      }]
    };
  },
  methods: {
    handleRouter(router) {
      console.log(router);
    }
  },
  computed: {
    ...mapState(["ruleConfigRightTableData"])
  }
};
</script>
<style media="screen">
  /*.right-wrapper::-webkit-scrollbar {
    display:none;
  }*/
  /*定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸*/
.right-wrapper::-webkit-scrollbar
{
    width: 6px;
    height: 4px;
    background-color: #F5F5F5;
}

/*定义滚动条轨道 内阴影+圆角*/
.right-wrapper::-webkit-scrollbar-track
{
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    border-radius: 2px;
    background-color: #F5F5F5;
}

/*定义滑块 内阴影+圆角*/
.right-wrapper::-webkit-scrollbar-thumb
{
    border-radius: 2px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
    background-color: #555;
}

</style>
<style lang="stylus" scoped>
.rule-config
  padding 20px 10px
  height 100%
.top-wrapper
  width: 100%;
  border-bottom: 1px solid #ccc;
.rules
  width 100%
  border 1px solid #ddd
  padding 10px
.all-info
  width 100%
  border 1px solid #ddd
  padding-bottom 10px
.rule-config
  // padding:10px 20px
.table-frame-table-item
  margin-bottom 10px
.right-col-wrapper
  height 100%
  .right-wrapper
    max-height 100%
    overflow-y auto


  // &::-webkit-scrollbar
  //   width: 16px
  //   height: 16px
  //   background-color: #F5F5F5
  //     /*定义滚动条轨道 内阴影+圆角*/
  // &::-webkit-scrollbar-track
  //   -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3)
  //   border-radius: 10px
  //   background-color: #F5F5F5
  // /*定义滑块 内阴影+圆角*/
  // &::-webkit-scrollbar-thumb
  //   border-radius: 10px
  //   -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3)
  //   background-color: #555

</style>



