<template>
  <div class="header-title">
    <div class="title-wrapper">
      <label>奖励结果:</label>
      <el-select class="select-result" v-model="value8" filterable placeholder="请选择" size="small">
      <el-option
      v-for="item in options1"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select>
    </div>
    <div class="title-wrapper">
      <el-select v-model="value1" filterable placeholder="请选择" size="small">
    <el-option
      v-for="item in options"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select>
    </div>
    <div class="add-btn">
      <el-button type="success" size="small" style="width:100%;">新增</el-button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      options1: [
        {
          value: "选项1",
          label: "调用接口"
        },
        {
          value: "选项2",
          label: "sql"
        }
      ],
      value8: "",
      options: [
        {
          value: "选项1",
          label: "增加积分"
        },
        {
          value: "选项2",
          label: "减少积分"
        },
        {
          value: "选项3",
          label: "增加积分记录"
        },
        {
          value: "选项4",
          label: "调用天猫接口"
        }
      ],
      value1: ""

    };
  }
};
</script>
<style lang='stylus'>
.header-title
  padding 10px 0px
  border-bottom 1px dotted #ddd
  .title-wrapper
    margin-bottom 10px
    label
      font-size 14px
    .select-result
      margin-top 5px
  .add-btn
      text-align center
</style>



